"use client";

import { useState } from 'react';
import { Conexao, CreateConexaoRequest } from '@/services/api/conexao.service';
import Input from '@/components/form/input/InputField';
import Button from '@/components/ui/Button';

type FormErrors = {
  nome?: string;
  numero?: string;
  isDefault?: string;
};

type ConexaoFormProps = {
  conexao?: Conexao;
  onSave: (data: CreateConexaoRequest) => Promise<void>;
  onCancel: () => void;
};

export default function ConexaoForm({ conexao, onSave, onCancel }: ConexaoFormProps) {
  const [formData, setFormData] = useState<CreateConexaoRequest>({
    nome: conexao?.nome || '',
    numero: conexao?.numero || '',
    isDefault: conexao?.isDefault || false
  });

  const [errors, setErrors] = useState<FormErrors>({});
  const [isSubmitting, setIsSubmitting] = useState(false);

  const validateForm = () => {
    const newErrors: FormErrors = {};
    const required = ['nome'];

    required.forEach(field => {
      if (!formData[field as keyof typeof formData]) {
        newErrors[field as keyof FormErrors] = `${field.charAt(0).toUpperCase() + field.slice(1)} é obrigatório`;
      }
    });

    if (formData.numero && formData.numero.length < 10) {
      newErrors.numero = 'Número deve ter pelo menos 10 caracteres';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!validateForm()) return;

    setIsSubmitting(true);
    try {
      await onSave(formData);
    } catch (error: any) {
      const erros = error.response?.data?.details || [];
      erros.forEach((err: any) => {
        const field = err.field.replace('body.', '');
        setErrors(prev => ({ ...prev, [field]: err.message }));
      });
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <div className="md:col-span-2">
          <Input
            id="nome"
            name="nome"
            hint="Nome da Conexão"
            value={formData.nome}
            onChange={(e: React.ChangeEvent<HTMLInputElement>) => 
              setFormData({ ...formData, nome: e.target.value })
            }
            error={errors.nome}
            placeholder="Ex: WhatsApp Principal"
          />
        </div>

        <div className="md:col-span-2">
          <Input
            id="numero"
            name="numero"
            hint="Número"
            value={formData.numero}
            onChange={(e: React.ChangeEvent<HTMLInputElement>) => 
              setFormData({ ...formData, numero: e.target.value })
            }
            error={errors.numero}
            placeholder="Ex: 5511999999999"
          />
        </div>

        <div className="md:col-span-2">
          <div className="flex items-center space-x-2">
            <input
              id="isDefault"
              name="isDefault"
              type="checkbox"
              checked={formData.isDefault}
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => 
                setFormData({ ...formData, isDefault: e.target.checked })
              }
              className="h-4 w-4 text-primary-600 focus:ring-primary-500 border-gray-300 rounded dark:border-gray-600 dark:bg-gray-700"
            />
            <label htmlFor="isDefault" className="text-sm font-medium text-gray-700 dark:text-gray-300">
              Conexão padrão
            </label>
          </div>
          <p className="mt-1 text-xs text-gray-500 dark:text-gray-400">
            Se marcado, esta será a conexão padrão para novas vendas sem conexão específica ou com conexão específica não conectada
          </p>
        </div>
      </div>

      <div className="flex justify-end space-x-4 pt-4">
        <Button
          type="button"
          variant="secondary"
          onClick={onCancel}
        >
          Cancelar
        </Button>
        <Button
          type="submit"
          variant="primary"
          disabled={isSubmitting}
        >
          {isSubmitting ? 'Salvando...' : conexao ? 'Atualizar' : 'Criar'}
        </Button>
      </div>
    </form>
  );
}
