"use client";

import React, { useState, useEffect, useRef, useCallback } from 'react';
import { Modal } from '@/components/ui/modal';
import Button from '@/components/ui/Button';
import { services } from '@/services/api';
import { Conexao } from '@/services/api/conexao.service';
import toast from 'react-hot-toast';
import Image from 'next/image';

interface QrCodeModalProps {
  isOpen: boolean;
  onClose: () => void;
  conexao: Conexao;
  onStatusUpdate: (updatedConexao: Conexao) => void;
}

const QrCodeModal = ({ isOpen, onClose, conexao, onStatusUpdate }: QrCodeModalProps) => {
  const [currentConexao, setCurrentConexao] = useState<Conexao>(conexao);
  const [loading, setLoading] = useState(false);
  const intervalRef = useRef<NodeJS.Timeout | null>(null);
  const [countdown, setCountdown] = useState(25);

  const fetchConexaoStatus = useCallback(async () => {
    try {
      setLoading(true);
      const response = await services.conexao.getById(conexao.id);
      setCurrentConexao(response.data);
      onStatusUpdate(response.data);
      
      // Se conectou, fechar o modal
      if (response.data?.instance?.state === 'open') {
        toast.success('Conexão estabelecida com sucesso!');
        onClose();
      }
    } catch (error) {
      console.error('Erro ao verificar status da conexão:', error);
      toast.error('Erro ao verificar status da conexão');
    } finally {
      setLoading(false);
    }
  }, [conexao.id, onStatusUpdate, onClose]);

  const startPolling = useCallback(() => {
    // Limpar intervalo existente se houver
    if (intervalRef.current) {
      clearInterval(intervalRef.current);
    }

    // Reset countdown
    setCountdown(25);

    // Criar novo intervalo
    intervalRef.current = setInterval(() => {
      fetchConexaoStatus();
      setCountdown(25); // Reset countdown após cada verificação
    }, 25000); // 25 segundos

    // Countdown timer
    const countdownInterval = setInterval(() => {
      setCountdown((prev) => {
        if (prev <= 1) {
          return 25;
        }
        return prev - 1;
      });
    }, 1000);

    return () => {
      clearInterval(countdownInterval);
    };
  }, [fetchConexaoStatus]);

  useEffect(() => {
    if (isOpen && currentConexao?.instance?.state === 'connecting') {
      // Buscar dados atualizados ao abrir o modal
      // fetchConexaoStatus();
      
      // Iniciar polling
      const cleanupCountdown = startPolling();
      
      return () => {
        if (intervalRef.current) {
          clearInterval(intervalRef.current);
        }
        cleanupCountdown();
      };
    }
    
    return () => {
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
      }
    };
  }, [isOpen, currentConexao?.instance?.state, fetchConexaoStatus, startPolling]);

  useEffect(() => {
    setCurrentConexao(conexao);
  }, [conexao]);

  const handleClose = () => {
    if (intervalRef.current) {
      clearInterval(intervalRef.current);
    }
    onClose();
  };

  const getStatusText = () => {
    switch (currentConexao?.instance?.state) {
      case 'connecting':
        return 'Aguardando conexão...';
      case 'open':
        return 'Conectado';
      default:
        return 'Desconectado';
    }
  };

  const getStatusColor = () => {
    switch (currentConexao?.instance?.state) {
      case 'connecting':
        return 'bg-blue-100 text-blue-800 border-blue-200';
      case 'open':
        return 'bg-green-100 text-green-800 border-green-200';
      default:
        return 'bg-red-100 text-red-800 border-red-200';
    }
  };

  return (
    <Modal isOpen={isOpen} onClose={handleClose} className="max-w-[700px] m-4">
      <div className="no-scrollbar relative w-full max-w-[700px] overflow-y-auto rounded-3xl bg-white p-4 dark:bg-gray-900 lg:p-8">
        <div className="text-center">
          <h4 className="mb-2 text-2xl font-semibold text-gray-800 dark:text-white/90">
            Conectar WhatsApp
          </h4>
          <p className="mb-6 text-sm text-gray-500 dark:text-gray-400">
            {currentConexao.nome}
          </p>

          {/* Status */}
          <div className="mb-6 flex justify-center">
            <div className={`inline-flex items-center px-3 py-1 rounded-full text-sm font-medium border ${getStatusColor()}`}>
              {getStatusText()}
            </div>
          </div>

          {/* QR Code ou Loading */}
          {currentConexao?.instance?.state === 'connecting' && (
            <div className="mb-6">
              {currentConexao.instance?.base64 ? (
                <div className="p-4 rounded-lg inline-block shadow-sm relative">
                  {loading && (
                    <div className="absolute inset-0 bg-white bg-opacity-75 flex items-center justify-center rounded-lg">
                      <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-500"></div>
                    </div>
                  )}
                  <Image
                    src={currentConexao.instance.base64}
                    alt="QR Code"
                    className="object-contain"
                    width={256}
                    height={256}
                  />
                  <div className="flex gap-1 w-full justify-center mt-2">
                    <p className='text-sm text-gray-500 dark:text-gray-400'>Codigo de Pareamento:</p>
                    <span className='text-sm text-gray-500 dark:text-gray-200'>
                      {currentConexao.instance.pairingCode || 'N/A'}
                    </span>
                  </div>
                </div>
              ) : (
                <div className="bg-gray-50 dark:bg-gray-800 p-8 rounded-lg border-2 border-dashed border-gray-200 dark:border-gray-600 w-72 h-72 flex items-center justify-center">
                  <div className="text-center">
                    <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-500 mx-auto mb-4"></div>
                    <p className="text-gray-600 dark:text-gray-400">
                      Gerando QR Code...
                    </p>
                  </div>
                </div>
              )}
              
              <div className="mt-4 text-center">
                <p className="text-sm text-gray-600 dark:text-gray-400 mb-2">
                  {currentConexao.instance?.base64 
                    ? 'Escaneie o QR Code com seu WhatsApp'
                    : 'Aguarde, o QR Code está sendo gerado'
                  }
                </p>
                {currentConexao.instance?.base64 && (
                  <div className="flex items-center justify-center gap-2">
                    <div className={`w-2 h-2 rounded-full ${loading ? 'bg-blue-500 animate-pulse' : 'bg-green-500'}`}></div>
                    <p className="text-xs text-gray-500 dark:text-gray-500">
                      {loading ? 'Atualizando...' : `Próxima atualização em ${countdown}s`}
                    </p>
                  </div>
                )}
              </div>
            </div>
          )}

          {/* Instruções */}
          {currentConexao?.instance?.state === 'connecting' && (
            <div className="mb-6 text-left bg-blue-50 dark:bg-blue-900/20 p-4 rounded-lg">
              <h5 className="font-medium text-blue-900 dark:text-blue-100 mb-2">
                Como conectar:
              </h5>
              <ol className="text-sm text-blue-800 dark:text-blue-200 space-y-1">
                <li>1. Abra o WhatsApp no seu celular</li>
                <li>{'2. Toque em "Mais opções" (⋮) → "Aparelhos conectados"'}</li>
                <li>{'3. Toque em "Conectar um aparelho"'}</li>
                <li>4. Escaneie o QR Code acima</li>
              </ol>
            </div>
          )}

          {/* Botões */}
          <div className="flex justify-center space-x-4">
            <Button
              variant="secondary"
              onClick={handleClose}
            >
              Fechar
            </Button>
            
            {currentConexao?.instance?.state === 'connecting' && (
              <Button
                variant="primary"
                onClick={fetchConexaoStatus}
                disabled={loading}
              >
                {loading ? 'Verificando...' : 'Atualizar'}
              </Button>
            )}
          </div>
        </div>
      </div>
    </Modal>
  );
};

export default QrCodeModal;
