"use client";

import React, { useState, useEffect, useCallback } from 'react';
import { services } from '@/services/api';
import toast from 'react-hot-toast';
import Button from '@/components/ui/Button';
import SearchInput from '@/components/form/input/SearchInput';
import { Modal } from '@/components/ui/modal';
import ConexaoCard from '@/components/conexoes/ConexaoCard';
import ConexaoForm from '@/components/conexoes/ConexaoForm';
import QrCodeModal from '@/components/conexoes/QrCodeModal';
import { Conexao, CreateConexaoRequest } from '@/services/api/conexao.service';

export default function ConexoesPage() {
  const [conexoes, setConexoes] = useState<Conexao[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(0);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isQrModalOpen, setIsQrModalOpen] = useState(false);
  const [selectedConexao, setSelectedConexao] = useState<Conexao | undefined>();
  const itemsPerPage = 9; // 3x3 grid

  const loadConexoes = useCallback(async () => {
    try {
      setLoading(true);
      const response = await services.conexao.list({
        page,
        limit: itemsPerPage,
        ...(searchTerm && { search: searchTerm })
      });
      setConexoes(response.data);
      setTotalPages(Math.ceil(response.pagination.total / itemsPerPage));
    } catch (error) {
      toast.error('Erro ao carregar conexões');
    } finally {
      setLoading(false);
    }
  }, [page, searchTerm, itemsPerPage]);

  useEffect(() => {
    loadConexoes();
  }, [loadConexoes]);

  const handleEdit = (conexao: Conexao) => {
    setSelectedConexao(conexao);
    setIsModalOpen(true);
  };

  const handleQrCode = (conexao: Conexao) => {
    setSelectedConexao(conexao);
    setIsQrModalOpen(true);
  };

  const handleStatusUpdate = (updatedConexao: Conexao) => {
    setConexoes(prev => 
      prev.map(c => c.id === updatedConexao.id ? updatedConexao : c)
    );
  };

  const handleRestart = async (id: number) => {
    if (!window.confirm('Tem certeza que deseja reiniciar esta conexão?')) return;

    try {
      await services.conexao.restart(id);
      toast.success('Conexão reiniciada com sucesso');
      loadConexoes(); // Recarregar lista para atualizar status
    } catch (error) {
      toast.error('Erro ao reiniciar conexão');
    }
  };

  const handleStop = async (id: number) => {
    if (!window.confirm('Tem certeza que deseja parar esta conexão?')) return;

    try {
      await services.conexao.stop(id);
      toast.success('Conexão parada com sucesso');
      loadConexoes(); // Recarregar lista para atualizar status
    } catch (error) {
      toast.error('Erro ao parar conexão');
    }
  };

  const handleDelete = async (id: number) => {
    if (!window.confirm('Tem certeza que deseja excluir esta conexão?')) return;

    try {
      await services.conexao.delete(id);
      toast.success('Conexão excluída com sucesso');
      loadConexoes();
    } catch (error) {
      toast.error('Erro ao excluir conexão');
    }
  };

  const handleSave = async (data: CreateConexaoRequest) => {
    try {
      if (selectedConexao) {
        await services.conexao.update(selectedConexao.id, data);
        toast.success('Conexão atualizada com sucesso');
      } else {
        await services.conexao.create(data);
        toast.success('Conexão criada com sucesso');
      }
      setIsModalOpen(false);
      setSelectedConexao(undefined);
      loadConexoes();
    } catch (error) {
      console.error('Erro ao salvar conexão:', error);
      toast.error('Erro ao salvar conexão');
    }
  };

  return (
    <div className="p-4 sm:p-6 lg:p-8">
      <div className="mb-8">
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
          <div>
            <h1 className="text-2xl font-semibold text-gray-900 dark:text-white">
              Conexões WhatsApp
            </h1>
            <p className="mt-1 text-sm text-gray-500 dark:text-gray-400">
              Gerencie suas conexões com a Evolution API
            </p>
          </div>
          <Button 
            variant="primary"
            onClick={() => {
              setSelectedConexao(undefined);
              setIsModalOpen(true);
            }}
          >
            Nova Conexão
          </Button>
        </div>

        <div className="mt-4 w-full max-w-sm">
          <SearchInput
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            placeholder="Buscar conexões..."
          />
        </div>
      </div>

      {loading ? (
        <div className="flex justify-center items-center h-64">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary-500"></div>
        </div>
      ) : (
        <>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {conexoes.map((conexao) => (
              <ConexaoCard
                key={conexao.id}
                conexao={conexao}
                onEdit={() => handleEdit(conexao)}
                onDelete={() => handleDelete(conexao.id)}
                onQrCode={() => handleQrCode(conexao)}
                onRestart={() => handleRestart(conexao.id)}
                onStop={() => handleStop(conexao.id)}
              />
            ))}
          </div>

          {conexoes.length === 0 && (
            <div className="text-center py-12">
              <p className="text-gray-500 dark:text-gray-400">
                Nenhuma conexão encontrada
              </p>
            </div>
          )}

          {totalPages > 1 && (
            <div className="mt-6 flex justify-center">
              <nav className="flex items-center gap-2">
                <Button
                  variant="secondary"
                  onClick={() => setPage((p) => Math.max(1, p - 1))}
                  disabled={page === 1}
                >
                  Anterior
                </Button>
                <span className="text-sm text-gray-700 dark:text-gray-300">
                  Página {page} de {totalPages}
                </span>
                <Button
                  variant="secondary"
                  onClick={() => setPage((p) => Math.min(totalPages, p + 1))}
                  disabled={page === totalPages}
                >
                  Próxima
                </Button>
              </nav>
            </div>
          )}
        </>
      )}

      <Modal 
        isOpen={isModalOpen}
        onClose={() => {
          setIsModalOpen(false);
          setSelectedConexao(undefined);
        }} 
        className="max-w-[800px] m-4"
      >
        <div className="no-scrollbar relative w-full overflow-y-auto rounded-3xl bg-white p-4 dark:bg-gray-900 lg:p-11">
          <div className="px-2 pr-14">
            <h4 className="mb-2 text-2xl font-semibold text-gray-800 dark:text-white/90">
              {selectedConexao ? 'Editar Conexão' : 'Nova Conexão'}
            </h4>
            <p className="mb-6 text-sm text-gray-500 dark:text-gray-400 lg:mb-7">
              {selectedConexao 
                ? 'Atualize as informações da conexão.' 
                : 'Preencha os dados para criar uma nova conexão.'
              }
            </p>
          </div>
        <ConexaoForm
          conexao={selectedConexao}
          onSave={handleSave}
          onCancel={() => {
            setIsModalOpen(false);
            setSelectedConexao(undefined);
          }}
        />
        </div>
      </Modal>

      {/* QR Code Modal */}
      {selectedConexao && (
        <QrCodeModal
          isOpen={isQrModalOpen}
          onClose={() => {
            setIsQrModalOpen(false);
            setSelectedConexao(undefined);
          }}
          conexao={selectedConexao}
          onStatusUpdate={handleStatusUpdate}
        />
      )}
    </div>
  );
}