import { Conexao } from '@/services/api/conexao.service';
import Button from '@/components/ui/Button';
import ButtonCard from '../ui/button/ButtonCard';

interface ConexaoCardProps {
  conexao: Conexao;
  onEdit: () => void;
  onDelete: () => void;
  onQrCode?: () => void;
  onRestart?: () => void;
  onStop?: () => void;
}

const ConexaoCard = ({
  conexao,
  onEdit,
  onDelete,
  onQrCode,
  onRestart,
  onStop,
}: ConexaoCardProps) => {
  const getStatusColor = () => {
    switch (conexao?.instance?.state) {
      case 'open':
        return 'bg-green-100 text-green-800 border-green-200';
      case 'connecting':
        return 'bg-blue-100 text-blue-800 border-blue-200';
      default:
        return 'bg-red-100 text-red-800 border-red-200';
    }
  };

  const getStatusText = () => {
    console.log('conexao:', conexao);
    switch (conexao?.instance?.state) {
      case 'open':
        return 'Conectado';
      case 'connecting':
        return 'Conectando...';
      default:
        return 'Desconectado';
    }
  };

  return (
    <div className="bg-white dark:bg-gray-800 rounded-lg shadow-sm hover:shadow-md shadow-gray-200 dark:shadow-gray-700 border border-gray-200 dark:border-gray-700 p-4">
      <div className="flex items-start justify-between">
        <div className="space-y-2">
          <div className="flex items-center gap-2">
            <h3 className="text-lg font-semibold text-gray-900 dark:text-white">
              {conexao.nome}
            </h3>
            {conexao.isDefault && (
              <span className="inline-flex items-center px-2 py-0.5 rounded-full text-xs font-medium bg-blue-100 text-blue-800 border border-blue-200">
                Padrão
              </span>
            )}
          </div>
          <p className="text-sm text-gray-500 dark:text-gray-400">
            {conexao.numero}
          </p>
          <div className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-medium border ${getStatusColor()}`}>
            Status: {getStatusText()}
          </div>
        </div>
        <div className="flex flex-wrap gap-2">
          {/* Botão QR Code para status connecting */}
          {conexao?.instance?.state === 'connecting' && onQrCode && (
            <ButtonCard
              size="xs"
              variant="success"
              onClick={onQrCode}
              title="Escanear QR Code"
              icon="qrcode" />
          )}

          {(conexao?.instance?.state === 'open' || conexao?.instance?.state === 'connecting') && (
            <>
              {onStop && (
                <ButtonCard
                  size="xs"
                  variant="secondary"
                  onClick={onStop}
                  title="Parar Conexão"
                  icon="stop" />
              )}
            </>
          )}
          {/* Botões de ação para conexões open */}
          {(conexao?.instance?.state === 'open' || conexao?.instance?.state === 'close') && (
            <>
              {onRestart && (
                <ButtonCard
                  size="xs"
                  variant="secondary"
                  onClick={onRestart}
                  title="Reiniciar Conexão"
                  icon="reload" />
              )}
            </>
          )}

          <ButtonCard
            size="xs"
            variant="secondary"
            onClick={onEdit}
            title="Editar Conexão"
            icon="edit" />

          <ButtonCard
            size="xs"
            variant="secondary"
            onClick={onDelete}
            title="Excluir Conexão"
            icon="delete" />
        </div>
      </div>
    </div>
  );
};

export default ConexaoCard;
